
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

@media screen and (min-width:1150px){
  nav {
    text-align: left;
    position: absolute;
    top: 100px;
    height: 240px;
    left: 140px;
    width: 120px;
    background: rgba(119, 119, 119, 0.596);
    border-radius: 8px;
    overflow-y: scroll;
  }
  nav a {
    text-decoration: none;
    color: black;
    font-weight: bold;
    padding: 18px;
    border-radius: 8px;
    display:block;
  }
  
  nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  nav li {
    display: list-item;
    padding-top: 16px;
  }

  link-bar {
      text-align: center;
      position: absolute;
      top: 100px;
      left: 1050px;
      width: 120px;
      background: rgba(119, 119, 119, 0);
      border-radius: 8px;
  }
  link-bar ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  link-bar li {
    display: list-item;
    padding-top: 16px;
    padding-bottom: 16px;
  }
  link-bar a {
    text-decoration: none;
    color: black;
    font-weight: bold;
    padding: 18px;
    border-radius: 8px;
    display: block;
  }
  #page {
    max-width: 700px;
    min-height: 400px;
    margin-left: 290px;
    margin-top: 65px;
    margin-right: 12px;
    padding: 13px;
  
    background-color: rgba(166, 205, 255, 0.9);
    border-radius: 8px;
  }
}

@media screen and (max-width:1149px){
  nav {
    text-align: left;
    position: absolute;
    top: 10px;
    height: 50px;
    right: 20%;
    left: 20%;
    width: 60%;
    background: rgba(119, 119, 119, 0.596);
    border-radius: 8px;
  }
  nav a {
    text-decoration: none;
    color: black;
    font-weight: bold;
    padding:8px;
    
    border-radius: 8px;
    display:inline;
    
  }
  
  nav ul {
    list-style-type: none;
    margin: 0;
    padding: 14px;
    text-align: center;
  }

  nav li {
    display: inline;
    padding-top: 6px;
  }

  link-bar {
    text-align: left;
    position: absolute;
    bottom: 23px;
    height: 50px;
    right: 20%;
    left: 20%;
    width: 60%;
    background: rgba(119, 119, 119, 0);
    border-radius: 8px;
  }
  link-bar ul {
    list-style-type: none;
    margin: 0;
    padding: 14px;
    text-align: center;
  }

  link-bar li {
    display: inline;
    padding-top: 6px;
  }
  link-bar a {
    text-decoration: none;
    color: black;
    font-weight: bold;
    padding-top: 35px;
    padding-bottom: 4px;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 8px;
    display: inline;
  }
  #page {
    width: 80%;
    min-height: 400px;
    margin-left: 10%;
    margin-top: 65px;
    margin-bottom:65px;
    margin-right: 10%;
    padding: 13px;
  
    background-color: rgba(166, 205, 255, 0.9);
    border-radius: 8px;
  }
}





nav a:hover {
  background: rgba(119, 119, 119, 0.9);
  color: rgb(0, 0, 0);
}



link-bar a:hover {
  background: rgba(119, 119, 119, 0.9);
  color: rgb(0, 0, 0);
}
#app{
  overflow-y: scroll;
  overflow-x: scroll;
  position: relative;

}



